<template>
    <form @submit.prevent="validateBeforeSubmit">
      <v-container>
        <v-layout row wrap>		
          <v-flex xs12><img src="../assets/logo-chsli.png" style="max-width:250px;" /></v-flex>        
        </v-layout>        

        <v-layout row wrap>		
          <v-flex xs12><strong><span style="font-size:1.5em;color:#336699;">Provider Loyalty Update</span></strong></v-flex>
        </v-layout>

        <v-layout row wrap>		
          <v-flex xs12 md8><v-text-field label="Provider Name" v-model="ref.providerName" name="Provider Name" ></v-text-field></v-flex>
          <v-flex xs12 md4><v-text-field label="NPI" v-model="ref.providerNPI" name="NPI" ></v-text-field></v-flex>
        </v-layout> 

        <v-layout row wrap>		
          <v-flex xs12><v-text-field label="Address" v-model="ref.providerAddress" name="Address" ></v-text-field></v-flex>
        </v-layout>      

        <v-layout row wrap>		
          <v-flex xs12 md6><v-text-field label="Phone" v-model="ref.providerPhone" name="Phone" ></v-text-field></v-flex>
          <v-flex xs12 md6><v-text-field label="Fax" v-model="ref.providerFax" name="Fax" ></v-text-field></v-flex>
        </v-layout> 

        <v-layout row wrap>		
          <v-flex xs12 md6><v-text-field label="Current Loyalty" v-model="ref.providerCurrentLoyalty" name="Current Loyalty" ></v-text-field></v-flex>
          <v-flex xs12 md6><v-text-field label="Effective Date" v-model="ref.providerEffectiveDate" name="Effective Date" ></v-text-field></v-flex>
        </v-layout>            

        <v-layout row wrap>
          <v-flex xs12>        
            <v-radio-group v-model="ref.providerNewLoyalty" row>
              <span style="margin-right:8px;color:rgba(0,0,0,0.54);">New Loyalty: </span>
              <v-radio label="Loyal" value="Loyal"></v-radio>
              <v-radio label="Splitter" value="Splitter"></v-radio>
              <v-radio label="Opportunity" value="Opportunity"></v-radio>
              <v-radio label="Out Of Network" value="Out Of Network"></v-radio>              
            </v-radio-group>
          </v-flex>  
        </v-layout>            

        <v-layout row wrap>
          <v-flex xs12>
            <v-menu
              ref="menuEffectiveDate"
              v-model="menuEffectiveDate"              
              :close-on-content-click="true"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px">
              <template v-slot:activator="{ on }">
                <v-text-field
                  id="effectiveDate"
                  v-model="ref.menuEffectiveDate"
                  slot="activator"                  
                  label="Effective Date"
                  name="Effective Date"
                  prepend-icon="event"   
                  readonly
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                ref="picker"   
                v-model="ref.menuEffectiveDate"
                max="2250-12-31"
                min="1980-01-01">
              </v-date-picker>
            </v-menu>
          </v-flex>
        </v-layout>




        <v-layout row wrap>		          
          <v-flex xs12>
            <ValidationProvider v-slot="v">
              <v-text-field label="Reason for Change/Comment" name="Reason for Change/Comment" ></v-text-field>
            </ValidationProvider>
          </v-flex>          
        </v-layout>

        <!--div>
          <ValidationProvider v-slot="v">
            <input v-model="ref.value" type="text">
          </ValidationProvider>
        </div-->

        <v-layout row wrap>
          <v-flex xs12>
              <v-btn @click="submitForm" color="normal">Submit Update</v-btn>
          </v-flex>
        </v-layout>

      </v-container>
    </form>
</template>

